.image_feedback{background-image:url("../assets/image/login/feedback.jpg")}.sec1_feedback{display:flex;width:80%;margin:0 10%;padding:5% 10%}.form_feedback{width:50%;padding:0 2%}.title_form_feedback{text-align:center;font-size:2rem;padding:.5rem 0 2rem;text-transform:uppercase}.feedback_list{width:50%;padding:2rem 2%;background-color:rgba(54,54,54,.027)}.box_image_user_feedback{width:20%;margin:0 40%;height:0;padding-bottom:20%;position:relative}.image_user_feedback{width:100%;height:100%;object-fit:cover;position:absolute;border-radius:50%}.user_name{text-align:center;padding:1rem 0;font-size:1rem;font-weight:bold}.line_feedback{display:flex;align-items:center;width:100%;margin:1rem 0}.label_feedback{display:inline-block;width:30%;font-size:1.3rem;font-weight:bold;line-height:2rem}.input_feedback{width:70%;height:2rem;box-sizing:border-box;outline:none;padding:0 .5rem}.fedbback_area{height:5rem}.box_btn_feedback{display:flex;justify-content:center}.btn_feedback{display:inline-block;border-radius:.2rem;background-color:#000;border:none;color:#fff;text-align:center;font-size:1rem;padding:.5rem;width:6rem;transition:all .5s;cursor:pointer;margin:5px}.btn_feedback_span{cursor:pointer;display:inline-block;position:relative;transition:.5s}.btn_feedback_span:after{content:"»";position:absolute;opacity:0;top:0;right:-1rem;transition:.5s}.btn_feedback:hover .btn_feedback_span{padding-right:1rem}.btn_feedback:hover .btn_feedback_span:after{opacity:1;right:0}.feedback_slick .slick-prev::before,.feedback_slick .slick-next::before{display:none}